<template>
  <div class="left-content" :key="instanceLKey">
    <GenerationStatistics class="section-box top-section" />
    <StationPowerStatistics class="section-box middel-section" />
    <SocialContribution class="section-box bottom-section" />
  </div>
  <div class="middle-content" :key="instanceTKey">
    <MiddleIndicator
      icon="gf-capacity"
      label="光伏装机(MW)"
      :value="topIndexData.gfCapacity"
      vColor="#67C8FF"
    />
    <MiddleIndicator
      icon="cn-capacity"
      label="储能装机(MW)"
      :value="topIndexData.cnCapacity"
      vColor="#F4D038"
    />
    <MiddleIndicator
      icon="safe-operation"
      label="安全运行(天)"
      :value="topIndexData.runningDays"
      vColor="#00BA24"
    />
  </div>
  <div class="right-content" :key="instanceRKey">
    <RealTimePower class="section-box top-section" />
    <GenerationEfficiency class="section-box middel-section" />
    <DisasterWarning class="section-box bottom-section" />
  </div>
</template>

<script lang="ts" setup>
  import { ref, reactive, UnwrapRef, onMounted } from 'vue';
  import GenerationStatistics from './GenerationStatistics.vue';
  import StationPowerStatistics from './StationPowerStatistics.vue';
  import SocialContribution from './SocialContribution.vue';
  import MiddleIndicator from '/@/components/Framework/OperationScreen/MiddleIndicator.vue';
  import RealTimePower from './RealTimePower.vue';
  import GenerationEfficiency from './GenerationEfficiency.vue';
  import DisasterWarning from './DisasterWarning.vue';
  import { getTopInfoData } from './data';
  import { assign } from 'lodash-es';
  import { buildUUID } from '@/utils/uuid';

  const instanceLKey = ref(buildUUID());
  const instanceRKey = ref(buildUUID());
  const instanceTKey = ref(buildUUID());
  const searchInfo: UnwrapRef<any> = reactive({});
  const topIndexData = reactive<any>({});

  // 获取顶端指标数据
  const queryTopInfoData = async () => {
    let data = await getTopInfoData(searchInfo);
    if (data.result) {
      assign(topIndexData, data.result);
    }
  };

  // 更新组件函数
  const handleUpdate = () => {
    instanceLKey.value = buildUUID();
    instanceRKey.value = buildUUID();
    instanceTKey.value = buildUUID();
  };

  defineExpose({
    handleUpdate,
  });

  onMounted(() => {
    searchInfo.statDimension = 'year';
    searchInfo.year = '2023';
    queryTopInfoData(); // 顶端指标
  });
</script>

